nav{
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translate(-50%);
    bottom: 2rem;
    display: flex;
    gap: 2rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}
nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 2rem;
}

nav a:hover{
    background:var(--color-primary);
    color: var(--color-bg);
}

nav a.active{
    background: rgba(26, 132, 219, 0.635);;
    color: var(--color-white);
}

/* RESPONSIVE DESIGN OF THE ABOUT PAGE  */


@media screen and (max-width : 600px) {
    nav{
        gap: 0.8rem;

    }
} 