
/* ####################   CTA    ############# */

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    /* justify-content: center; */
}

/* #############  HEADER SOCIAL ############## */

.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header_socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.header_container .me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem ;
    border-radius: 12rem 12rem 0 0 ;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* ##############  scroll down  ############# */

.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform:rotate(90deg) ;
    font-weight: 300;
    font-size: 0.9rem;
}



/************ Banner Css ************/
.banner {
    margin-top: -2rem;
    padding: 260px 0 100px 0;
    background-position: top center;
    background-size: cover;
    background-image: url('../../assets/banner-bg.png');
    background-repeat: no-repeat;
    box-shadow: 0px -65px 51px 9px #1f1f38 inset;
}

.aligh-items-center{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.banner .tagline {
    font-weight: 700;
    letter-spacing: 0.8px;
    padding: 8px 10px;
    background: linear-gradient(90.21deg, rgba(170, 54, 124, 0.5) -5.91%, rgba(74, 47, 189, 0.5) 111.58%);
    border: 1px solid rgba(255, 255, 255, 0.5);
    font-size: 10px;
    margin-bottom: 16px;
    display: inline-block;
}

.banner h1 {
    font-size: 50px;
    font-weight: 700;
    letter-spacing: 1.8px;
    line-height: 1.3;
    margin-bottom: 20px;
    display: block;
}

.banner p {
    color: #B8B8B8;
    font-size: 18px;
    letter-spacing: 0.8px;
    line-height: 1.5em;
    width: 96%;
}

.banner button {
    color: #fff;
    font-weight: 700;
    font-size: 20px;
    margin-top: 60px;
    letter-spacing: 0.8px;
    display: flex;
    align-items: center;
}

.banner button svg {
    font-size: 25px;
    margin-left: 10px;
    transition: 0.3s ease-in-out;
    line-height: 1;
}

.banner button:hover svg {
    margin-left: 25px;
}

.banner img {
    animation: updown 3s linear infinite;
}

@keyframes updown {
    0% {
        transform: translateY(-20px);
    }

    50% {
        transform: translateY(20px);
    }

    100% {
        transform: translateY(-20px);
    }
}

.txt-rotate>.wrap {
    border-right: 0.08em solid #666;
}


/* #################  media query of the website  ############### */

@media screen and (max-width: 1024px) {
    header {
        height: 100vh;
    }

    .aligh-items-center {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }

}


@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header_socials,
    .scroll_down {
        display: none;
    }

    .aligh-items-center {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
    .banner h1 {
        font-size: 25px;
        font-weight: 500;
        letter-spacing: 1.3px;
        line-height: 1.1;
        text-align: center;
    }
    .cta {
        justify-content: center;
    }
    .banner p{
        text-align: center;
    }

}